	module.exports = {"data":{"markdownRemark":{"html":"<p>It's not quite like a teaching book of 3DCG for games. There are plenty of ways to draw 3DCG, but there are not many things written assuming to be used in games, while there are many 3D program books, but almost none at all if it comes to modeling.</p>\n<ul>\n<li><a href=\"http://www.amazon.co.jp/%E3%82%B2%E3%83%BC%E3%83%A0CG%E6%95%99%E7%A7%91%E6%9B%B8-%E2%80%95%E3%82%AD%E3%83%A3%E3%83%A9%E3%82%AF%E3%82%BF%E3%83%BC%E3%83%BB%E8%83%8C%E6%99%AF%E7%B7%A8%E2%80%95-%E6%9D%89%E5%8E%9F-%E7%94%B1%E7%BE%8E%E5%AD%90/dp/4939007766/sr=8-1/qid=1169515055/ref=sr_1_1/249-5114787-5895507?ie=UTF8&#x26;s=books\">Game CG textbooks Although</a> I bought one book that was eventually the only one, I felt that I was able to read through it carefully It feels quite general generic 3DCG's teaching book. Are you really trying out by trial and error? Well I can not even return the sound of a musical instrument if it is told that it should be the original form</li>\n</ul>","frontmatter":{"cover":null,"date":"2007-01-23T10:19:00+09:00","strDate":"2007/1/23","title":"3DCG drawing method (WANTED)"}}},"pathContext":{"path":"/en/blog/2007/01/23-3dcg描き方の本wanted/","langKey":"en"}}

